import { Outlet, useNavigate, useLocation } from "react-router-dom";
import { useState, createContext, useReducer, useEffect } from 'react';
import { ToastContainer, toast } from 'react-toastify';
import loading from "../img/loading.svg"
import axios from "axios"
import "../components/footerControl/footerControl.css"
import 'react-toastify/dist/ReactToastify.css';
import axiosRetry from "axios-retry";
import {
    useQueries,
    QueryClient,
    QueryClientProvider
} from '@tanstack/react-query'
import "./Root.css"

const initialState = {
    page: "main_page",
    invoiceDone: 0,
    invose: false,
    applications: false,
    userInfo: null,
    token: null,
    userBalance: null,
    userPhoto: null,
    projects: []
}

const queryClient = new QueryClient()

function reducer(state, action) {
    switch (action.type) {
        case "add_projects":
            return { ...state, projects: action.payload }
        case "set_user_photo":
            return { ...state, userPhoto: action.payload }
        case "set_token":
            return { ...state, token: action.payload }
        case "add_user_names":
            return { ...state, userInfo: action.payload }
        case "add_user_balance":
            return { ...state, userBalance: action.payload }
        case "switch_page":
            return { ...state, page: action.payload }
        case "open_invose":
            return { ...state, invose: true, invoiceDone: action.payload }
        case "close_invose":
            return { ...state, invose: false, invoiceDone: null }
        case "open_my_applications":
            return { ...state, applications: true }
        case "close_my_applications":
            return { ...state, applications: false }
        case "success_popup":
            toast(action.payload, { autoClose: 3000 })
            return { ...state }
        case "error_popup":
            toast.error(action.payload, { autoClose: 3000 })
            return { ...state }
        default:
            console.warn("Error in app reducer")
    }
}
const fakeUser = { "user": { "id": 5432487039, "first_name": "NaN", "last_name": "NaN", "username": "NaN913", "language_code": "ru", "allows_write_to_pm": true }, "chat_instance": "4313923184418680035", "chat_type": "private", "auth_date": "1731331405", "hash": "85c60210be701b40325096b35a8912b2bba50cd37f4aba3f584626ed5c043900" }
export const AppState = createContext(initialState)

export default function Root() {
    axiosRetry(axios, { retries: 5 });
    const [state, dispatch] = useReducer(reducer, initialState)
    const [compState, setCompState] = useState({
        page: "main_page",
        userData: null,
        respons: null,
        token: null
    })
    const location = useLocation()
    const navigate = useNavigate()

    function initToken(value) {
        if (state.token === null) {
            axios.post("https://api.sprite-ps.com/app_auth"
                , value
            ).then(response => {
                dispatch({ type: "set_token", payload: response.headers['authorization'] })
                setCompState({ ...compState, token: response.headers['authorization'] })
                localStorage.setItem("TokenBearer", response.headers['authorization'])
            }
            ).catch(error => {
                if (error.status === 403) {
                    dispatch({ type: "set_token", payload: "error" })
                    navigate("/telegram-cabinet/fail_auth")
                } else {
                    console.log("Request error " + error.status)
                    initToken(value)
                }

            })
        }
    }

    useEffect(() => {
        console.log("~~~~RenderedWebApp~~~~")
        if (window.Telegram && window.Telegram.WebApp) {
            console.log(window.Telegram.WebApp.initDataUnsafe)
            console.log("sending token")
            window.Telegram.WebApp.ready();
            window.Telegram.WebApp.expand();
            window.Telegram.WebApp.setHeaderColor("#141723")
            window.Telegram.WebApp.setBackgroundColor("#141723")
            window.Telegram.WebApp.BackButton.onClick()
            window.Telegram.WebApp.enableClosingConfirmation()
            initToken(window.Telegram.WebApp.initDataUnsafe)
        }
    }, [location.pathname])
    useEffect(() => {
        if (location.pathname !== '/') {
            window.Telegram.WebApp.BackButton.show()
        } else {
            window.Telegram.WebApp.BackButton.hide()
        }
    }, [location.pathname])

    if (state.token === null) {
        return (
            <div className="block spinner-load">
                <div className="spinner-wrapper">
                    <span className="spinner-title">Loading</span>
                    <img src={loading} alt="loading" className="spinner" />
                </div>

            </div>
        )
    } else {
        return (
            <AppState.Provider value={{ data: state, dispatch: dispatch }}>
                <ToastContainer
                    position="top-left"
                    autoClose={3000}
                    hideProgressBar={false}
                    newestOnTop={false}
                    closeOnClick
                    rtl={false}
                    pauseOnFocusLoss={false}
                    draggable={false}
                    pauseOnHover={false}
                    theme="dark"
                />
                <div className="mainFrame">
                    <QueryClientProvider client={queryClient}>
                        <Outlet />
                    </QueryClientProvider>
                </div >
            </AppState.Provider >
        );

    }

}