import { useNavigate } from "react-router-dom";
import { useContext } from "react";
import { AppState } from "../routes/Root";
import { useState, useEffect } from "react";
import axios from "axios";

export default function useGetUserData(value, rerender = "false", method = "GET") {
    const context = useContext(AppState)
    const navigate = useNavigate()
    const [compState, setCompState] = useState({
        userData: null
    })
    function backStory() {
        navigate(-1)
    }
    useEffect(() => {
        axios(value, {
            method: method,
            headers: {
                "Authorization": context.data.token
            }
        })
            .then(response => setCompState({ ...compState, userData: response.data }))
            .catch(error => console.log("Error in hook " + error.status))
    }, [rerender])

    return compState.userData
}